import React, {Component} from 'react';
import { Card,Dropdown, DropdownButton, Spinner } from 'react-bootstrap';


class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = {
          show: true,
          categories: []
        };
        this.showModal = this.showModal.bind(this);
        this.hideModal = this.hideModal.bind(this);
        this.handleCategoryChange = this.handleCategoryChange.bind(this);

      }

    componentDidMount = () => {
        // Get categories
        this.getCategories();
    }

    showModal = () => {
        this.setState({ show: true });
    };

    hideModal = () => {
        this.setState({ show: false });
    };

    handleCategoryChange = (e) => {
        this.props.handleCategoryChange(e)
    };

    getCategories = async () => {
        // Category lookup https://opentdb.com/api_category.php
        fetch("https://opentdb.com/api_category.php")
        .then(res => res.json())
        .then(
        (res) => {
            for (let index in res.trivia_categories){
                this.setState({
                    categories: this.state.categories.concat(res.trivia_categories[index].name)
                })  
            }
        });

        this.setState({
            isLoaded: true
        })  

      
    }

    render() {
        const { isLoaded } = this.state;

        if (!isLoaded || this.state.categories.length === 0) {
            return (
            <>
            <h3>Loading</h3>
            <h5><Spinner animation="grow" variant="info" size="sm"/>   <Spinner animation="grow" variant="info" size="sm"/>   <Spinner animation="grow" variant="info" size="sm"/></h5>
              </>
            )
        } else {
            return (
            <form onSubmit={this.hideModal}>
            <Card show={this.state.show}>
                <Card.Header>
                <Card.Title>Trivia</Card.Title>
                {this.state.categories.map((cat, index) => {
                    return <> {this.state.categories[index]} <br/> </>
                })}
                </Card.Header>
                <Card.Body>
                <Dropdown >
                    <Dropdown.Header>Category</Dropdown.Header>
                    <DropdownButton 
                        variant="success" 
                        id="dropdown-basic"
                        title={this.props.category}
                        onSelect={this.handleCategoryChange}
                    >
                        {this.state.categories.map((cat, index) => {
                            return <Dropdown.Item eventKey={this.state.categories[index]}>{this.state.categories[index]}</Dropdown.Item>
                        })}
                        <Dropdown.Item eventKey="General Knowledge" >General Knowledge</Dropdown.Item>
                        <Dropdown.Item eventKey="Mythology" >Mythology</Dropdown.Item>
                        <Dropdown.Item eventKey="Sports" >Sports</Dropdown.Item>
                    </DropdownButton>
                </Dropdown>
                <p>Press Start to Play</p>
                </Card.Body>
                <Card.Footer>
                    <button onClick={this.props.startGame}>Start</button>
                </Card.Footer>
            </Card>
            </form>
        );
        }
    }
}
   
export default Menu;